perspective-viewer[theme="Monokai"] regular-table::-webkit-scrollbar-thumb,
.perspective-viewer-monokai regular-table::-webkit-scrollbar-thumb {
  background-color: transparent;
}
perspective-viewer[theme="Monokai"] regular-table:hover::-webkit-scrollbar-thumb,
.perspective-viewer-monokai regular-table:hover::-webkit-scrollbar-thumb {
  background-color: #e0e4e9;
}
perspective-viewer[theme="Monokai"] regular-table,
.perspective-viewer-monokai regular-table {
  --rt-pos-cell--color: #7dc3f0;
  --rt-neg-cell--color: #ff9485;
}
perspective-viewer[theme="Monokai"] regular-table table,
.perspective-viewer-monokai regular-table table {
  color: white;
}
perspective-viewer[theme="Monokai"] regular-table table tr:hover,
.perspective-viewer-monokai regular-table table tr:hover {
  color: white;
}
perspective-viewer[theme="Monokai"] regular-table table tbody tr td,
.perspective-viewer-monokai regular-table table tbody tr td,
perspective-viewer[theme="Monokai"] regular-table table tbody tr th,
.perspective-viewer-monokai regular-table table tbody tr th {
  --rt-hover--border-color: #61656e;
  border-top-color: #3b3f46;
}
