.perspective-viewer-monokai regular-table:hover::-webkit-scrollbar-thumb {
  background-color: #3b3f46;
}
perspective-viewer[theme="Monokai"] tbody th:empty,
.perspective-viewer-monokai tbody th:empty {
  background: linear-gradient(to right, transparent 9px, #3b3f46 10px, transparent 11px);
  background-repeat: no-repeat;
  background-position: 0px -10px;
}
perspective-viewer[theme="Monokai"] regular-table,
.perspective-viewer-monokai regular-table {
  --rt-pos-cell--color: #78dce8 !important;
  --rt-neg-cell--color: #ff6188 !important;
